.woorule-subscribe {
	width: 100%;
	display: block;
}

.woorule-subscribe form {
	width: 100%;
	display: flex;
	flex-wrap: wrap;
}

.woorule-subscribe label, .woorule-subscribe p {
	flex: 0 1 100%;
}

.woorule-subscribe .hidden {
	display: none;
}

.woorule-subscribe .success {
	color: green;
}

.woorule-subscribe .error {
	color: red;
}
